import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router";

import Input from "../../components/UI/Input/Input";
import Button from "../../components/UI/Button/Button";
import Spinner from "../../components/UI/Spinner/Spinner";
import classes from "./Auth.module.css";
import * as actions from "../../store/actions/index";

class Auth extends Component {
  state = {
    controls: {
      email: {
        elementType: "input",
        elementConfig: {
          type: "email",
          placeholder: "Your e-mail address",
        },
        value: "",
        validation: {
          required: true,
          minLength: 8,
          isEmail: true,
        },
        valid: false,
        touched: false,
        errorMessage: "Please, enter something like: test@test.com",
      },
      password: {
        elementType: "input",
        elementConfig: {
          type: "password",
          placeholder: "Your Password",
        },
        value: "",
        validation: {
          required: true,
          minLength: 6,
        },
        valid: false,
        touched: false,
        errorMessage: "Too short",
      },
    },
    isValid: false,
    isSignup: true,
  };

  componentDidMount() {
    if (!this.props.burgerBuilding && this.props.redirectPath !== "/")
      this.props.onRedirectPath("/");
  }

  checkValidity(value, rules) {
    let isValid = true;
    if (!rules) {
      return true;
    }

    if (rules.required) {
      isValid = value.trim() !== "" && isValid;
    }

    if (rules.minLength) {
      isValid = value.length >= rules.minLength && isValid;
    }

    if (rules.maxLength) {
      isValid = value.length <= rules.maxLength && isValid;
    }

    if (rules.isEmail) {
      const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
      isValid = pattern.test(value) && isValid;
    }

    if (rules.isNumeric) {
      const pattern = /^\d+$/;
      isValid = pattern.test(value) && isValid;
    }

    return isValid;
  }

  inputChangeHandler = (e, inputIdentifier) => {
    const formCopy = { ...this.state.controls };
    const inputCopy = { ...formCopy[inputIdentifier] };
    inputCopy.value = e.target.value;
    inputCopy.valid = this.checkValidity(inputCopy.value, inputCopy.validation);
    inputCopy.touched = true;
    formCopy[inputIdentifier] = inputCopy;

    this.setState({ controls: formCopy });
  };

  submitHandler = (e) => {
    e.preventDefault();
    this.props.onStartAuth(
      this.state.controls.email.value,
      this.state.controls.password.value,
      this.state.isSignup
    );
  };

  switchAuthCasesHandler = () => {
    this.setState((prevState) => {
      return {
        isSignup: !prevState.isSignup,
      };
    });
  };

  render() {
    const formElementsArray = Object.entries(this.state.controls).map((arr) => {
      return {
        key: arr[0],
        elementType: arr[1].elementType,
        elementConfig: arr[1].elementConfig,
        value: arr[1].value,
        valid: arr[1].valid,
        validation: arr[1].validation,
        touched: arr[1].touched,
        errorMessage: arr[1].errorMessage,
      };
    });
    let form = formElementsArray.map((el) => {
      return (
        <Input
          key={el.key}
          elementType={el.elementType}
          elementConfig={el.elementConfig}
          value={el.value}
          invalid={!el.valid}
          shouldValidate={el.validation}
          isTouched={el.touched}
          errorMessage={el.errorMessage}
          changed={(event) => this.inputChangeHandler(event, el.key)}
        />
      );
    });
    let errorMessage;
    let redirectIfAuth;

    if (this.props.loading) form = <Spinner />;
    if (this.props.error) errorMessage = <p>{this.props.error.message}</p>;
    if (this.props.isAuthenticated)
      redirectIfAuth = <Redirect to={this.props.redirectPath} />;

    return (
      <div className={classes.Auth}>
        {redirectIfAuth}
        {errorMessage}
        <form onSubmit={(e) => this.submitHandler(e)}>
          {form}
          <Button btnType={"Success"}>
            {this.state.isSignup ? "Sign up" : "Sign in"}
          </Button>
        </form>
        <Button btnType={"Danger"} clicked={this.switchAuthCasesHandler}>
          Switch to {this.state.isSignup ? "Sign in" : "Sign up"}
        </Button>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    loading: state.auth.loading,
    error: state.auth.error,
    isAuthenticated: state.auth.token !== null,
    burgerBuilding: state.burgerBuilder.building,
    redirectPath: state.auth.redirectPath,
  };
};
const mapDispatchToProps = (dispacth) => {
  return {
    onStartAuth: (email, password, isSignup) =>
      dispacth(actions.auth(email, password, isSignup)),
    onRedirectPath: (path) => dispacth(actions.authRedirectPath(path)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Auth);



